import { mapState, mapMutations } from 'vuex'
import ListResume from '@/components/ListResume/ListResume.vue'
import Search from '@/components/Search/Search.vue' 
import uniPopup from "@/components/uni-popup/uni-popup.vue"
import Dialog from '@/components/Dialog/Dialog.vue'
import ResumeModel from '@/models/resume.js'
const resumeModel = new ResumeModel()
export default {
	data() {
		return {
			resumeList: [
				{
					type: 'started'
				},
				{
					type: 'notStart'
				}
			],
			type: 'started',
			pageNum: 1,
			pageSize: 10,
			showDialog: false
		}
	},
	components:{
		ListResume,
		Search,
		uniPopup,
		Dialog
	},
	computed:{
		...mapState(['currentResume', 'jobId']),
		
	},
	mounted(){
		this.getResumeList()
	},
	methods: {
		...mapMutations(['switchTab']),
		search(keyword){
			// 做搜索动作
			
		},
		getResumeList(){
			const type = this.type
			let status = ''
			let name = ''
			let phone = ''
			if(type === 'started'){
				status = 1
			}else if(type === 'notStart'){
				status = 0
			}
			resumeModel.resumeList(this.pageNum,this.pageSize,status,name,phone).then(res=>{
				//数据绑定
				const { code, message, data } = res.data
				if(code === '0'){
					this.resumeList = data
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.showToast({
					icon: 'none',
					title:'获取简历信息失败'
				})
			})
		},
		switchTab(type){
			if(type === this.type){
				return 
			}
			this.type = type
			this.pageNum = 1
			this.getResumeList()
		},
		showRecord(){
			this.$refs.recommendRecord.open()
		},
		closeRecommendDialog(){
			this.$refs.recommendRecord.close()
		},
		showDetail(id){
			console.log("进入方法")
			uni.navigateTo({
				url: '../../public/addResume/addResume?isEdit=false&id='+id,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		recommendHim(){
			
		},
		showAddResume(){
			uni.navigateTo({
				url: '../../public/addResume/addResume?isEdit=true',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}
